<template>
    <el-table :data= this.coursesInfo height="250" style="width: 100%">
      <el-table-column prop="department_name" label="学院" sortable width="200"/>
      <el-table-column prop="major_name" label="专业" sortable width="150"/>
      <el-table-column prop="course_id" label="课程代号" sortable width="180" />
      <el-table-column prop="course_name" label="课程名称" sortable width="180" />
      <el-table-column prop="credit" label="学分" sortable />
      <el-table-column prop="teacher_name" label="教师" sortable />
      <el-table-column prop="max_student_number" label="容量" sortable />
    </el-table>
</template>




<script>
import student_api from '@/api/student/student-api'
import student_function from '@/api/student/student-function'
export default{
    data(){
        return{
            user_id:"",
            coursesInfo:[],
        }
    },
    mounted() {
    this.user_id = this.$route.query.user_id
    student_function.身份校验检查(this,this.user_id)

    student_api.获取所有课程().then(resp=>{
        if(resp.status_code == 200){
            this.coursesInfo = resp.data
        }
        else{
            student_function.Alert_Error(this,resp.data)
        }
    })
  },



}


</script>








